import { Section3 } from "../../components/landing/section3";
import { Section4 } from "../../components/landing/section4";
export const Landing = () => {
  useEffect(() =>{
    window.scrollTo({
      top: 0,
    });
  },[])
  return (
    <div className="download">
      <Banner/>
      <div className="description">
        <div>
          <img src={grayBidu} alt="" />
        </div>
        <div className="position-absolute col-12 col-md-8 col-lg-6 d-flex justify-content-center align-items-center">
          <img src={leftComma} alt="" />
          <div className="text_community">
            BIDU tin rng cm hng thi trang c th n t bt k ai, bt k
            ni no trong cuc sng ca mi ngi. Chng ti mong mun xy dng
            mt ng dng ni m ngi dng c th t tin th hin c tnh ca
            ring h thng qua thi trang mt cch t do v ng thi truyn cm
            hng cho mi ngi xung quanh.
          </div>
          <img src={rightComma} alt="" />
        </div>
      </div>
      <Section3/>
      <Section4/>
    </div>
  );
};
